import { Component } from '@angular/core';

@Component({
  selector: 'app-home',
  templateUrl: './home.component.html',
  styleUrls: ['./home.component.css']
})
export class HomeComponent {
  registerMode = false;

  constructor() {}

  enterRegisterMode() {
    this.registerMode = true;
  }

  exitRegisterMode() {
    this.registerMode = false;
  }
}
